.app-container {
  height: 100vh;
}

.input {
  width: 50%;
  margin: 0 10px;
}
.section {
  display: flex;
  flex-direction: row;
  justify-content: center;
  width: 100%;
}

.tab {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 20%;
  border: 3px solid black;
  margin: 5px auto;
  padding: 5px;
  font-size: 14px;
  cursor: pointer;
}

.hide {
  display: none;
}

.resume {
  display: flex;
  flex-direction: row;
  justify-content: flex-end;
}

.container {
  display: flex;
  flex-direction: row;
}

img {
  height: 80vh;
  margin: 0 5px;
}

.data-tab {
  margin: 20px 20px 0px;
  font-size: 20px;
  font-weight: bold;
}

h4 {
  margin: 5px 0;
}
